import React from 'react'
import { Link } from 'react-router-dom';

import Carousel from "react-bootstrap/Carousel";
import carousel00 from "../../../img/carousel00.png";
import carousel01 from "../../../img/carousel01.png";
import carousel02 from "../../../img/carousel02.png";

import Style from "./HomeCarousel.module.scss"

const HomeCarousel: React.FC = () => {
    return (
        <div className={Style.container}>
            <Carousel fade>
                <Carousel.Item>
                    <div className={Style.carItemCont}>
                        <img className={Style.carImgCont} src={carousel00} alt="carousel00" />
                        <div className={Style.carItemText}>
                            <p>Akcis kinlat</p>
                            <h2>DELPHIN NYELETFKES ORSK </h2>
                            <h4>
                                Vlogs tbb 100 akcis Delphin nyeletfkes orsink kzl.
                            </h4>
                            <Link to="/Orsk/101001219">
                                <button className={Style.homeCarBtn}>MEGNZEM</button>
                            </Link>
                        </div>
                    </div>
                </Carousel.Item>
                <Carousel.Item>
                    <div className={Style.carItemCont}>
                        <img className={Style.carImgCont} src={carousel01} alt="carousel00" />
                        <div className={Style.carItemText}>
                            <p>jonnan a knlatba</p>
                            <h2>CARP ZOOM Satellite 301 3+1</h2>
                            <h4>
                                Megrkezett az j CARP ZOOM Satellite 301 3+1 kapsjelz szett,
                                mely akr bvthet 4+1-ig.
                            </h4>
                            <Link to="/Kiegsztk/101001363">
                                <button className={Style.homeCarBtn}>MEGNZEM</button>
                            </Link>
                        </div>
                    </div>
                </Carousel.Item>
                <Carousel.Item>
                    <div className={Style.carItemCont}>
                        <img className={Style.carImgCont} src={carousel02} alt="carousel00" />
                        <div className={Style.carItemText}>
                            <p>Akcis kinlat</p>
                            <h2>DELPHIN THORN HOOK</h2>
                            <h4>
                                Akcis az egyik legjobb pontyoz horog. Tbb mretben
                                rendelhet.
                            </h4>
                            <Link to="/kiegsztk/101001456">
                                <button className={Style.homeCarBtn}>MEGNZEM</button>
                            </Link>
                        </div>
                    </div>
                </Carousel.Item>
            </Carousel>
        </div>
    )
}
export default HomeCarousel;