import Modal from 'react-bootstrap/Modal';
import { useShopContext } from "../../../../context/ShopContext";
import style from "./LoginInfoModal.module.scss";
import "../../../../scss/ancestor-class.scss"
const LoginInfoModal: React.FC = () => {

    const {
        loginMessage,
        modalInfo,
        toggleInfoModal,
        toggleDropdownLogin
    } = useShopContext();

    const textTSX = () => {
        switch (loginMessage[0]) {
            case "successful": {
                return ["Sikeres Bejelentkezs!", "J vsrlst", "Rendben"]
            }
            case "error": {
                return ["Sikerestelen Bejelentkezs!", "Hibs felhasznlnv vagy jelsz.", "rtettem"]
            }
            case "incomplete": {
                return ["Sikerestelen Bejelentkezs!", "Tltsn ki a felhasznlnevet s a jelszt.", "rtettem"]
            }
            case "reg-incomplete": {
                return ["Sikerestelen Regisztrci!", "Tltsn ki minden * jellt mezt.", "rtettem"]
            }
            case "reg-successful": {
                return ["Sikeres Regisztrci!", "Jelentkeze be a folytatshoz", "Bejelentkezs"]
            }
            case "reg-error": {
                return ["Sikerestelen Regisztrci!", "Valami hiba trtnt a regisztrci kzben", "rtettem"]
            }
            case "reg-error-password": {
                return ["Sikerestelen Regisztrci!", "A kt jelsz nem egyezik meg!", "rtettem"]
            }
            case "reg-error-existingEmail": {
                return ["Sikerestelen Regisztrci!", `A ${loginMessage[1]} mr foglalt emailcm, krjk jelentkezzen be.`, "rtettem"]
            }
            default: {
                return ["HIBA", "Valami nincs rendben jjjn vissza ksbb.", "rtettem"]
            }
        }
    }

    const text = textTSX();

    const buttonFunction = () => {
        if (loginMessage[0] === "reg-successful") {
            toggleDropdownLogin()
            toggleInfoModal()
        } else {
            toggleInfoModal()
        }
    }

    return (
        <div>
            <Modal show={modalInfo} onHide={toggleInfoModal}>
                <Modal.Header className="backgroundModal" closeButton>
                    <Modal.Title>
                        {text[0]}
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body className="backgroundModal">
                    {text[1]}
                </Modal.Body>
                <Modal.Footer className="backgroundModal">
                    <button className={style.btn} onClick={buttonFunction}>
                        {text[2]}
                    </button>
                </Modal.Footer>
            </Modal>
        </div>
    );
}

export default LoginInfoModal;